import React from 'react';
import { useState, useEffect, useRef } from "react";

export function Timer(props) {
    const didMount = useRef()
    const [startTimer, setStartTimer] = useState(false)
    const [removeTimer, setRemoveTimer] = useState(false)
    const [time, setTime] = useState('Select time')
    const [finishingTime, setFinishingTime] = useState(0)

    let zeroLength = 2;
    let interval = useRef();

    //* remove timer
    useEffect(() => {
        if(didMount.current) {
            props.setSaveLocalTime((prev) => {
                return {
                    ...prev, 
                    [props.typeTimer]: [
                        ...prev[props.typeTimer].filter(item => item.id !== props.item.id)
                    ]
                }
            })
            
            didMount.current = false
            setRemoveTimer(false)
        } 
        
    }, [removeTimer])

    //* setFinishingTime
    useEffect(() => {
        if (didMount.current) {
            const [id, hours, minutes, seconds] = time.split('_')

            setFinishingTime(finishingTime.setHours(finishingTime.getHours() + Number(hours)))
            setFinishingTime(finishingTime.setMinutes(finishingTime.getMinutes() + Number(minutes)))
            setFinishingTime(finishingTime.setSeconds(finishingTime.getSeconds() + Number(seconds)))
        }

    }, [startTimer])

    //* TimerOn/Off
    useEffect(() => {
        if (didMount.current) {
            interval.current = setInterval(() => {
                const currentTime = new Date()
                let countDownTime = new Date()

                // 

                countDownTime.setHours(finishingTime.getHours() - currentTime.getHours())
                countDownTime.setMinutes(finishingTime.getMinutes() - currentTime.getMinutes())
                countDownTime.setSeconds(finishingTime.getSeconds() - currentTime.getSeconds())

       
                //! GMT
                // countDownTime.setHours(countDownTime.getHours() + currentTime.getTimezoneOffset() / 60)

                if (finishingTime.getTime() - currentTime.getTime() > 0) {
                    props.setSaveLocalTime((prev) => {
                        let test = [...prev[props.typeTimer]]
                        test[props.id] = {
                            id: props.id,
                            hours: countDownTime.getHours(),
                            minutes: countDownTime.getMinutes(),
                            seconds: countDownTime.getSeconds()
                        }

                        return {
                            ...prev, 
                            [props.typeTimer]: [...test]
                            
                        }
                    })
                } else {
                    props.setSaveLocalTime((prev) => {
                        let test = [...prev[props.typeTimer]]
                        test[props.id] = {
                             id: props.id,
                                    hours: countDownTime.getHours(),
                                    minutes: countDownTime.getMinutes(),
                                    seconds: countDownTime.getSeconds()
                        }

                        return {
                            ...prev, 
                            [props.typeTimer]: [...test]
                            
                        }
                    })

                    console.log('yay')
                    clearInterval(interval.current)
                }
            }, 1000)

        } else {
            didMount.current = true
        }
        
        return () => {
            clearInterval(interval.current)
        }

    }, [startTimer])

    return (
        <div className='timer'>
            <h2>{`
                ${String(props.item.hours).padStart(zeroLength, '0')}:
                ${String(props.item.minutes).padStart(zeroLength, '0')}:
                ${String(props.item.seconds).padStart(zeroLength, '0')}
            `}</h2>

            {
                startTimer ? <button onClick={() => {
                    didMount.current = false
                    setTime('Select time')
                    setStartTimer(false)
                    clearInterval(interval)
                    setFinishingTime(0)
                    // setRemainingTime({
                    //     ...remainingTime,
                    //     remainingSeconds: '00',
                    //     remainingMinutes: '00',
                    //     remainingHours: '00'
                    // })
                }}>Cancel</button>

                :
            
                <> 
                    <select onClick={(e) => {
                        setTime(e.target.options[e.target.selectedIndex].value)
                    }}>                   
                        <option>Select time</option>
                        {
                            props.times.map((item, i) => {
                                return <option key={i} value={item.value}>{item.text}</option>
                            })
                        }  
                    </select>
            
                    <button disabled={time === 'Select time' ? true : false} onClick={() => {
                        setStartTimer(true)
                        setFinishingTime(new Date())
                    }}>Go</button>
                </>
            }

            <button onClick={() => {
                clearInterval(interval)
                setRemoveTimer(true)
            }}>Del</button>
        </div>
    )
}